@import "https://fonts.googleapis.com/css2?family=Outfit:wght@400;700&display=swap";
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Outfit;
}

body .container {
  height: 100vh;
  width: 100%;
  background-color: #d6e2f0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

body .qr-card {
  width: 220px;
  height: fit-content;
  padding: 13px;
  padding-block-end: 0;
  background-color: #fff;
  border-radius: 12px;
  padding-bottom: 0;
}

body .qr-card .qr-img {
  width: fit-content;
  border-radius: 10px;
}

body .qr-card .qr-img img {
  max-width: 100%;
  max-height: 100%;
  border-radius: inherit;
}

body .qr-card .qr-text {
  margin-bottom: 20px;
  padding: 0 12px;
  font-size: 15px;
}

body .qr-card .qr-text p {
  text-align: center;
  font-weight: 700;
}

body .qr-card .qr-text p:nth-child(2) {
  color: #7b879d;
  text-align: center;
  font-size: 12px;
  font-weight: 400;
}

body .attribution {
  text-align: center;
  font-size: 11px;
  position: relative;
  top: 20px;
}

body .attribution a {
  color: #3e52a3;
}

